header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden; 
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;   
    justify-content: center;
}

/* Header Socials*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 8rem;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

/* ME */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 24rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 13rem 13rem 5rem 5rem;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;  /*edited*/
}

/* Scroll Down */

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIA QUERIES FOR MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;        
    }     
}

/* MEDIA QUERIES FOR SMALL DEVICES */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials, 
    .scroll_down {
        display: none;
    }
}